.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
}
.modal{
    position: fixed;
    top: 20vh;
    left: 25%;
    width: 90%;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
}